介護医療院、介護老人保健施設の従事者についても、医療機関と同一敷地内にある場合には、医療機関の判断により対象となることがあります。上記の施設にお勤めである場合、実際に接種できるかどうかは勤務先や所属先に御確認ください。
医療従事者等の範囲において、社会福祉協議会等がおこなう介護サービスのヘルパーは対象外ですか。
訪問介護の従事者は医療従事者等には含まれません。
