現時点では、高齢者施設等の従事者は、高齢者に次ぐ接種順位と位置付けられています。実施時期は現時点では未定です。ただし、条件の整った高齢者施設では、入所者の方への接種と同じタイミングで、施設従事者の方も接種する方向で調整を進めております。
