訪問介護事業所や居宅介護支援事業所等の居宅サービス事業所等の従業者のうち、一定の要件に該当する方については、高齢者施設等の従事者に含めてよいこととされています。接種に関しては勤務先や所属先からお知らせできるよう調整中です。
高齢者施設等で従事している者の対象施設の範囲が示されていますが、国から示されていない施設で従事している者は接種できないのでしょうか。
現時点では高齢者施設等の従事者の順位には当てはまりません。
