高齢者施設の入所者へ接種を行う際、その従事者も一緒に接種できますか。
施設内感染防止の観点から、施設や医療機関と連携して体制を構築し、入所されている方に加えて、施設で従事されている職員等についても、同じタイミングで接種できるよう調整を進めております。
